<template>
  <div v-if="tweet.retweeted_by && tweet.retweeted_by !== ''">
    <!-- Retweet header -->
    <div class="pt-4 pl-4 flex flex-row">
      <div class="w-12 mr-4 flex justify-end">
        <i class="text-sm pt-1 fas fa-retweet text-dark"></i>
      </div>
      <p class="text-sm text-dark">{{ label }}</p>
    </div>
  </div>
  <div
      class="w-full p-2 pt-1 pb-1 md:p-4 md:pt-2 md:pb-2 border-b hover:bg-lightest flex"
  >
    <div class="flex-none mr-2 md:mr-4 pt-1">
      <a>
        <img
          :src="`${this.profile.avatar || 'default_profile.png'}`"
          class="h-12 w-12 rounded-full flex-none"
        />
      </a>
    </div>
    <div class="w-full">
      <div class="flex items-center w-full">
        <a @click="gotoProfile(tweet.user_id)">
          <p class="font-semibold" >{{ tweet.username }}</p>
        </a>
        <p class="hidden md:block text-sm text-dark ml-2">
          @{{ tweet.user_id }}
        </p>
        <p class="text-sm text-dark ml-2">·</p>
        <p class="text-sm text-dark ml-2">{{ $filters.timeago(tweet.created_at) }}</p>
        <div>
          <i @click="showDropdown = !showDropdown" class="fas fa-angle-down text-sm ml-auto text-dark cursor-pointer"></i>
          <div v-if="showDropdown" class="absolute right-0 mt-2 w-48 bg-white rounded-md shadow-lg py-1">
            <a @click="this.delete" class="block px-4 py-2 text-sm text-gray-700 hover:bg-gray-100">Delete tweet</a>
          </div>
        </div>
      </div>
        <p class="pb-2">{{ tweet.text }}</p>
      <div class="flex w-full">
        <div class="flex items-center text-sm text-dark w-1/4">
          <button
            @click="replyToTweet"
            class="mr-2 rounded-full hover:bg-lighter"
          >
            <i class="far fa-comment"></i>
          </button>
          <p v-if="getRepliesCount(tweet.id) > 0">{{ getRepliesCount(tweet.id) }}</p>
        </div>
        <div class="flex items-center text-sm text-dark w-1/4">
          <button
            @click="retweet()"
            class="mr-2 rounded-full hover:bg-lighter"
          >
            <i
              :class="
                `fas fa-retweet ${tweet.retweeted_by ? 'text-green-500' : ''}`
              "
            ></i>
          </button>
          <p v-if="getRetweetsCount(tweet.id) > 0">{{ getRetweetsCount(tweet.id) }}</p>
        </div>
        <div class="flex items-center text-sm text-dark w-1/4">
          <button
            @click="like()"
            class="mr-2 rounded-full hover:bg-lighter"
          >
            <i :class="`fas fa-heart ${getLikesCount(tweet.id) > 0 ? 'text-red-600' : ''}`"></i>
          </button>
          <p v-if="getLikesCount(tweet.id) > 0">{{ getLikesCount(tweet.id) }}</p>
        </div>
        <div class="flex items-center text-sm text-dark w-1/4">
          <i class="fas fa-share-square mr-3"></i>
        </div>
      </div>
    </div>
    <ReplyOverlay v-if="showReplyOverlay" :tweet="tweet" :profile="profile" :showReplyOverlay="showReplyOverlay" @update:showReplyOverlay="showReplyOverlay = $event" />
  </div>
</template>

<script>
import ReplyOverlay from "./ReplyOverlay.vue";
import {mapActions} from "vuex";

export default {
  name: "Tweet",
  props: ["tweet"],
  components: {
    ReplyOverlay,
  },
  data() {
    return {
      profile: {},
      likersCursor: "",
      retweetersCursor: "",
      showReplyOverlay: false,
      showDropdown: false,
      label: "You Retweeted",
      likesCount: new Map(),
      retweetsCount: new Map(),
      repliesCount: new Map(),
      likers: new Map(),
      retweeters: new Map()
    };
  },
  methods: {
    ...mapActions("warpnet", [
      "getProfile",
      "likeTweet", 
      "unlikeTweet",
      "deleteTweet",
      "getTweet",
      "getTweetStats",
      "getLikesNum",
      "getLikers",
      "retweetTweet",
      "unretweetTweet", 
      "getRetweetsNum",
      "getRetweeters",
      "getTweetReply",
      "deleteTweetReply",
    ]),
    gotoProfile(tweetUserId) {
      this.$router.push({
        name: "Profile",
        params: {
          id: tweetUserId,
        },
      });
    },
    async get() {
      let t = undefined
      if (!this.tweet.parent_id || this.tweet.parent_id === this.tweet.root_id) {
        const getObject = {
          userId: this.tweet.user_id,
          tweetId: this.tweet.id,
        }
        t =  await this.getTweet(getObject);
      } else {
        const getObject = {
          rootId: this.tweet.root_id,
          replyId: this.tweet.id,
        }
        t =  await this.getTweetReply(getObject);
      }
      this.showReplyOverlay = true;
    },
    async delete() {
      if (!this.tweet.parent_id || this.tweet.parent_id === '') {
        const deleteObject = {
          userId: this.tweet.user_id,
          tweetId: this.tweet.id,
        }
        await this.deleteTweet(deleteObject);
        this.gotoProfile(this.profile.id);
      } else {
        const deleteObject = {
          userId: this.profile.id,
          rootId: this.tweet.root_id,
          replyId: this.tweet.id,
        }
        await this.deleteTweetReply(deleteObject);
      }
    },
    replyToTweet() {
      this.showReplyOverlay = true;
    },
    async retweet() {
      let retweetsNum = 0
      if (!this.retweeters.has(this.tweet.id)) {
        console.error("failed to retweet tweet", this.tweet.id);
        return;
      }
      if (!this.retweeters.get(this.tweet.id).has(this.tweet.user_id) || this.tweet.retweeted_by === '') {
        const retweetObject = {
          tweetId: this.tweet.id,
          userId: this.tweet.user_id,
          username: this.tweet.username,
          text: this.tweet.text,
        }

        retweetsNum = await this.retweetTweet(retweetObject).catch((err) => {
          console.error(`failed to retweet [${this.tweet.id}]`, err);
        });
        this.retweeters.get(this.tweet.id).set(this.tweet.user_id, this.profile)
      } else {
        retweetsNum = await this.unretweetTweet(this.tweet.id).catch((err) => {
          console.error(`failed to unretweet [${this.tweet.id}]`, err);
        });
        this.retweeters.get(this.tweet.id).delete(this.tweet.user_id)

      }
      this.retweetsCount.set(this.tweet.id, retweetsNum)
    },
    async like() {
      let likesNum = 0
      if (!this.likers.has(this.tweet.id)) {
        console.error("failed to like tweet", this.tweet.id);
        return;
      }
      if (!this.likers.get(this.tweet.id).has(this.tweet.user_id)) {
        likesNum = await this.likeTweet(this.tweet.id).catch((err) => {
          console.error(`failed to like tweet [${this.tweet.id}]`, err);
        });
        this.likers.get(this.tweet.id).set(this.tweet.user_id, this.profile)
      } else {
        likesNum = await this.unlikeTweet(this.tweet.id).catch((err) => {
          console.error(`failed to unlike tweet [${this.tweet.id}]`, err);
        });
        this.likers.get(this.tweet.id).delete(this.tweet.user_id)
      }

      this.likesCount.set(this.tweet.id, likesNum);
    },
    getLikesCount(tweetId) {
      return this.likesCount.get(tweetId);
    },
    getRetweetsCount(tweetId) {
      return this.retweetsCount.get(tweetId);
    },
    getRepliesCount(tweetId) {
      return this.repliesCount.get(tweetId);
    },
  },
  async created() {
    this.profile = await this.getProfile(this.tweet.user_id);

    if (this.tweet.retweeted_by !== this.profile.id) {
      this.label = `${this.tweet.username} Retweeted`;
    }
    this.likers.set(this.tweet.id, null);
    this.retweeters.set(this.tweet.id, null);

    const stats = await this.getTweetStats({tweetId:this.tweet.id, limit:20, cursor:""});

    this.likesCount.set(stats.tweet_id, stats.likes_count);
    this.retweetsCount.set(stats.tweet_id, stats.retweets_count);
    this.repliesCount.set(stats.tweet_id, stats.replies_count);
    // this.viewsCount.set(stats.tweet_id, stats.views_count); // TODO

    this.likersCursor = stats.likers.cursor;
    const likersMap = new Map();
    for (const userId of stats.likers.users || []) {
      likersMap.set(userId, null);
    }
    this.likers.set(this.tweet.id, likersMap);

    this.retweetersCursor = stats.retweeters.cursor;
    const retweetersMap = new Map();
    for (const userId of stats.retweeters.users || []) {
      retweetersMap.set(userId, null);
    }
    this.retweeters.set(this.tweet.id, retweetersMap);
  },
};
</script>
